import React, { Component } from 'react';
import { Icon, Button } from 'antd';
import throttle from 'lodash/throttle';

export interface IBackTopProps {
    target?: string;
    threshold?: number;
    scrollStep?: number;
    delayMs?: number;
}

class BackTop extends Component<IBackTopProps> {
    private content: Element;
    private timeoutId: NodeJS.Timeout;
    private intervalId: NodeJS.Timeout;

    static defaultProps: IBackTopProps = {
        threshold: 64,
        target: '.ant-layout-content',
        scrollStep: 100,
        delayMs: 16,
    }

    componentDidMount() {
        const { target } = this.props;
        this.content = document.querySelector(target);
        if (this.content) {
            this.attachEvent(this.content);
        }
    }

    attachEvent = (content) => {
        content.addEventListener('scroll', this.onScroll);
    }

    onScroll = throttle((e: any) => {
        const { threshold } = this.props;
        if (e.target.scrollTop >= threshold) {
            const content = document.querySelector('.blog-backtop');
            content.classList.add('visible');
            if (this.timeoutId) {
                clearTimeout(this.timeoutId);
            }
            this.timeoutId = setTimeout(() => {
                const content = document.querySelector('.blog-backtop');
                if (content) {
                    content.classList.remove('visible');
                }
            }, 1500);
        } else {
            const content = document.querySelector('.blog-backtop');
            content.classList.remove('visible');
        }
    }, 200)

    scrollStep = () => {
        this.content.scrollTo(0, 0);
    }

    scrollToTop = () => {
        this.scrollStep();
    }
      
    render() {
        return (
            <Button className="blog-backtop" type="primary" shape="circle" onClick={this.scrollToTop}>
                <Icon type="to-top" style={{ fontSize: '1.25rem' }} />
            </Button>
        );
    }
}

export default BackTop;